











































import { Component, Vue } from "vue-property-decorator";

@Component
export default class extends Vue {
  tags: String[] = [];
  tagsDel: String[] = [];
  type: String = "";
  //初始化 tags type
  created() {
    if (this.$route.query.type === "支出") {
      this.tags = this.$store.state.tagsOut;
      this.tagsDel = this.$store.state.tagsOutDel;
    } else {
      this.tags = this.$store.state.tagsIn;
      this.tagsDel = this.$store.state.tagsInDel;
    }
    this.type = this.$route.query.type as String;
  }

  back() {
    this.$router.back();
  }
  removeTag(item: String, index: number) {
    this.$store.commit("tagsDel", [item, this.type]);
  }
  pushTag(item: String, index: number) {
    this.$store.commit("tagsAdd", [item, this.type]);
  }
  addTag() {
    const item_name = window.prompt();
    if (item_name) {
      this.$store.commit("tagsPush", [item_name, this.type]);
    }
  }
  change(type: String) {
    if (this.type === type) {
      return;
    } else {
      this.type = type;
      if (this.type === "支出") {
        this.tags = this.$store.state.tagsOut;
        this.tagsDel = this.$store.state.tagsOutDel;
      } else {
        this.tags = this.$store.state.tagsIn;
        this.tagsDel = this.$store.state.tagsInDel;
      }
    }
  }
}
